import React from 'react'
import { CustomTable, CustomTabs } from '../../reuseableComponents'

const MyRequests = (props) => {
    const { myRequestsTabs, myRequestData,currentPage ,setCurrentPage} = props

    const myRequestField = [
        { key: 'created_at', label: 'Generated On' },
        { key: 'points', label: 'Points' },
        { key: 'points_value', label: 'Points Value' },
        { key: 'mobile', label: 'Mobile' },
        { key: 'redeem_type', label: 'Redeem Type' },
        { key: 'value', label: 'Value' },
        { key: 'remark', label: 'Remark' },
    ]
    const myRequestScopedSlots = {
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),
        action: (item, index) => (
            <span> </span>
        ),
    };
    return (
        <div>
            <div className="cardBox mb-5">
                <div className="cardHead d-flex align-items-center justify-content-between">
                    <h3>
                        My Requests{" "}
                        <span
                            data-bs-toggle="tooltip"
                            data-bs-placement="top"
                            data-bs-title="Kudos request expire in 30 days prior there generation"
                        >
                            <i className="bi bi-info-circle-fill" />
                        </span>
                    </h3>
                    <div className="dropdown monthDropdown">
                        <button
                            type="button"
                            className="dropdown-toggle"
                            data-bs-toggle="dropdown"
                        >
                            Last 6 Month
                        </button>
                        <div className="dropdown-menu">
                            <ul>
                                <li>
                                    <a className="dropdown-item" href="#">
                                        Today
                                    </a>
                                </li>
                                <li>
                                    <a className="dropdown-item" href="#">
                                        Yesterday
                                    </a>
                                </li>
                                <li>
                                    <a className="dropdown-item" href="#">
                                        This Month
                                    </a>
                                </li>
                                <li>
                                    <a className="dropdown-item" href="#">
                                        Last Month
                                    </a>
                                </li>
                                <li>
                                    <a className="dropdown-item" href="#">
                                        Last 3 Month
                                    </a>
                                </li>
                                <li>
                                    <a className="dropdown-item" href="#">
                                        Last 6 Month
                                    </a>
                                </li>
                                <li>
                                    <a className="dropdown-item" href="#">
                                        Last Year
                                    </a>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
                <CustomTabs tabs={myRequestsTabs} type="myRequests" ulClass={"requesttab"} />

                <div className="tab-content">
                    <div className="tab-pane fade show active" id="pending">
                        <div className="table-responsive tableDiv">
                            <CustomTable
                                data={myRequestData || []}
                                fields={myRequestField}
                                // isLoading={isLoading}
                                pagination={true}
                                currentPage={currentPage}
                                setCurrentPage={setCurrentPage}
                                totalPages={myRequestData?.totalPages}
                                isHeader={false}
                                tableStyle={{ padding: "0px", boxShadow: "none" }}
                                scopedSlots={myRequestScopedSlots}
                            />
                        </div>
                    </div>
                    <div className="tab-pane fade" id="approved">
                        <CustomTable
                            data={myRequestData || []}
                            fields={myRequestField}
                            // isLoading={isLoading}
                            pagination={true}
                            currentPage={currentPage}
                            setCurrentPage={setCurrentPage}
                            totalPages={myRequestData?.totalPages}
                            isHeader={false}
                            tableStyle={{ padding: "0px", boxShadow: "none" }}
                            scopedSlots={myRequestScopedSlots}
                        />
                    </div>

                </div>
            </div>
        </div>
    )
}

export default MyRequests
