import React, { useState } from 'react'
import MyRequests from './MyRequests'
import ApprovalRequests from './ApprovalRequests'

const RedeemPointStatement = () => {
    const [myRequestData, setMyRequestData] = useState('')
    const [approvalRequestData, setApprovalRequestData] = useState('')
    /* My Request  */
    const myRequestsTabs = [
        { name: "Pending", onClickFun: (id) => getMyRequestData(id), id: "pending" },
        { name: "Approved ", onClickFun: (id) => getMyRequestData(id), id: "approved" },
    ];

    const getMyRequestData = (type) => {


    }
    /* Approval Request */
    const approvalRequestsTabs = [
        { name: "Pending", onClickFun: (id) => getApprovalRequestData(id), id: "pending" },
        { name: "Approved ", onClickFun: (id) => getApprovalRequestData(id), id: "approved" },
    ];

    const getApprovalRequestData = (type) => {


    }
    return (
        <div>
            <MyRequests
                myRequestsTabs={myRequestsTabs}
                myRequestData={myRequestData}
            />
            <ApprovalRequests
                approvalRequestsTabs={approvalRequestsTabs}
                approvalRequestData={approvalRequestData}
            />
        </div>
    )
}

export default RedeemPointStatement
